.mechanics{
    padding-top: 40px;
    &__title{
        margin-bottom: 111px;
    }
}
.section__title{
    text-align: center;
    font-weight: 800;
    font-size: 48px;
    line-height: 56px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 0px 4px 30px rgba(9, 42, 66, 0.5);
    position: relative;
    z-index: 1;
    img{
        width: 100%;
        position: absolute;
        left: 0;
        top: -140px;
        display: block;
        z-index: -1;
        max-height: 264px;
    }
}
.m-title{
    font-weight: 800;
    font-size: 32px;
    line-height: 37px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 0px 4px 10px rgba(9, 42, 66, 0.5);
    width: 100%;
    display: inline-block;
}

.steps{
    position: relative;
    padding-top: 90px;
    &__container{
        width: 1174px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        padding-bottom: 120px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center bottom;
    }
    &__info{
        position: absolute;
        left: 0;
        top: -40px;
    }
}
.step{
    padding-left: 85px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__num{
        position: absolute;
        top: -90px;
        left: 25px;
        font-weight: 800;
        font-size: 398px;
        line-height: 466px;
        text-align: center;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.1);
        text-shadow: 0px 33.1996px 248.997px rgba(9, 42, 66, 0.2);
    }
    &__text{
        width: 285px;
        font-weight: bold;
        font-size: 22px;
        line-height: 26px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        a{
            text-decoration: underline;
            color: #fff;
        }
        span{
            font-weight: bold;
            font-size: 15px;
            line-height: 26px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF;
            display: block;
        }
    }
    &__bg{
        width: 191px;
        height: 157px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 14px;
    }
}

.week{
    position: relative;
    z-index: 2;
    margin-bottom: 65px;
    .m-title{
        margin-bottom: 40px;
    }
    &__prizes{
        padding-top: 60px;
        .wrapper{
            display: flex;
            justify-content: space-between;
        }
    }
    &__bg{
        img{
            display: block;
            position: absolute;
            &:first-child{
                right: 0;
                top: -380px;
            }
            &:nth-child(2){
                left: 0;
                top: -140px;
            }
        }
    }
    &__prize{
        display: flex;
        flex-direction: column;
        align-items: center;
        &:nth-child(1){
            margin-top: -47px;
            .week__prize-text{
                max-width: 150px;
            }
            .week__prize-img{
                max-width: 230px;
            }
        }
        &:nth-child(2){
            margin-top: -16px;
            .week__prize-text{
                max-width: 124px;
            }
            .week__prize-img{
                max-width: 184px;
            }
        }
        &:nth-child(3){
            .week__prize-text{
                max-width: 225px;
            }
            .week__prize-img{
                max-width: 355px;
            }
        }
        &:nth-child(4){
            margin-top: -16px;
            .week__prize-text{
                max-width: 116px;
            }
            .week__prize-img{
                max-width: 219px;
            }
        }
        &:nth-child(5){
            margin-top: -86px;
            .week__prize-text{
                max-width: 219px;
            }
            .week__prize-img{
                max-width: 218px;;
            }
        }
        &-img{
            img{
                max-width: 100%;
            }
        }
        &-text{
            font-weight: bold;
            font-size: 12px;
            line-height: 15px;
            text-align: center;
            color: #FFFFFF;
        }
    }
}
.cash{
    padding-top: 80px;
    padding-bottom: 240px;
    background: linear-gradient(90deg, #028312 9.16%, #058414 24.99%, #1A961F 50%, #24A126 69.04%, #1A951F 83.32%);
    position: relative;
    .cash__prizes .wrapper{
        display: flex;
        justify-content: center;
    }
    .cash__prize{
        margin-left: 30px;
        margin-right: 30px;
        &:nth-child(1){
            .cash__prize-text{
                max-width: 353px;
            }
        }
        &:nth-child(2){
            .cash__prize-text{
                max-width: 275px;
            }
        }
        .cash__prize-text{
            text-align: center;
            margin: 0 auto;
            font-weight: 600;
            font-size: 20px;
            line-height: 23px;
            text-align: center;
            color: #FFFFFF;
        }

    }
    .cash__prize-img{
        width: 404px;
        height: 248px;
        img{
            max-width: 100%;
            max-height: 100%;
        }
    }
    &__bg{
        img{
            display: block;
            position: absolute;
            &:nth-child(1){
                top: -202px;
                left: 0;
            }
            &:nth-child(2){
                top: -103px;
                right: 0;
            }
            &:nth-child(3){
                top: 597px;
                left: 0;
            }
            &:nth-child(4){
                top: 623px;
                right: 0;
            }

        }
    }
}