html,
body {
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  min-height: 100vh;
  position: relative;
  font-family: "Circe", sans-serif;
  -webkit-overflow-scrolling: touch;
  background: linear-gradient(90deg, #007CD7 9.16%, #008ADB 24.99%, #009FE1 50%, #00B4E7 69.04%, #00A9E4 83.32%);
  padding-top: 93px;
}
body > * {
  flex-shrink: 0;
}

.no-scroll {
  overflow: hidden;
}

* {
  outline: none !important;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}

input[type=text],
input[type=email],
input[type=password] {
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

input::-ms-clear {
  display: none;
}

input::-ms-reveal {
  display: none;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.wrapper {
  width: 1280px;
  margin: 0 auto;
}

.section__title img {
  pointer-events: none;
}

.banner {
  padding-top: 58px;
  position: relative;
  z-index: 1;
}
.banner .video {
  width: 398px;
  height: 200px;
}
.banner .wrapper {
  position: relative;
}
.banner__img {
  position: absolute;
  left: 400px;
  top: -58px;
  z-index: -1;
}
.banner__years {
  position: absolute;
  top: 0px;
  left: 1135px;
}
.banner__text {
  font-weight: bold;
  font-size: 19px;
  line-height: 16px;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-bottom: 42px;
}

.title {
  font-weight: 800;
  font-size: 77px;
  line-height: 71px;
  text-transform: uppercase;
  color: #FFFFFF;
  max-width: 530px;
  width: 100%;
  margin-bottom: 34px;
}

.button {
  display: inline-block;
  width: 398px;
  text-align: center;
  padding-bottom: 20px;
  padding-top: 25px;
  font-weight: bold;
  font-size: 33px;
  line-height: 35px;
  text-transform: uppercase;
  color: #000000;
  background: #F8D428;
  margin-bottom: 48px;
  transition: 0.3s;
}
.button:hover {
  background: #efc708;
}

.video {
  position: relative;
  cursor: pointer;
  display: block;
}
.video__bg {
  width: 100%;
  height: 100%;
  position: relative;
}
.video__bg::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: #000;
  opacity: 0.5;
  transition: 0.3s;
}
.video__bg img {
  width: 100%;
  height: 100%;
  display: block;
}
.video__btn {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.video__title {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 18px 27px;
  font-weight: bold;
  font-size: 30px;
  line-height: 34px;
  text-transform: uppercase;
  color: #FFFFFF;
  background: rgba(255, 0, 7, 0.9);
}
.video:hover .video__bg::before {
  opacity: 0.4;
}

.footer {
  padding: 33px 0;
  margin-top: auto;
  background: linear-gradient(90deg, #00A9E4 9.16%, #00B4E7 24.99%, #009FE1 50%, #008ADB 69.04%, #007CD7 83.32%);
}
.footer .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__title {
  font-size: 21px;
  line-height: 28px;
  color: #FFFFFF;
}

.info__text {
  font-size: 17px;
  line-height: 23px;
  color: #FFFFFF;
  margin-bottom: 10px;
}
.info__text span {
  font-weight: bold;
}

.politic {
  display: inline-block;
  font-size: 14px;
  line-height: 15px;
  border-bottom: 1px solid #fff;
  color: #FFFFFF;
  margin-bottom: 10px;
  transition: 0.3s;
}
.politic:hover {
  border-color: transparent;
}

.copyright {
  font-size: 14px;
  line-height: 15px;
  color: #FFFFFF;
}

.tel__number {
  font-size: 42.5893px;
  line-height: 57px;
  text-align: center;
  color: #FFFFFF;
}
.tel .footer__title {
  text-align: center;
  width: 100%;
}

.social__container {
  display: flex;
}
.social__item {
  margin-right: 10px;
  transition: 0.3s;
  cursor: pointer;
}
.social__item:hover {
  transform: scale(1.1);
}

.header {
  width: 100%;
  height: 93px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.25);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 5;
}
.header.scrolled .header__button {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}
.header__block {
  width: 300px;
}
.header__button {
  width: 253px;
  font-weight: bold;
  font-size: 20px;
  line-height: 22px;
  text-align: center;
  display: inline-block;
  background: #F8D428;
  padding-top: 12px;
  padding-bottom: 9px;
  color: #000;
  opacity: 0;
  pointer-events: auto;
  visibility: visible;
  transition: 0.3s;
}
.header__button:hover {
  background: #efc708;
}

.logo {
  display: inline-block;
}

.nav {
  display: flex;
}
.nav__link {
  position: relative;
  cursor: pointer;
  margin-right: 25px;
  font-size: 16px;
  line-height: 25px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}
.nav__link::before {
  position: absolute;
  left: 50%;
  width: 0;
  height: 1px;
  bottom: 4px;
  background: #000;
  content: "";
  transition: 0.3s;
}
.nav__link:hover::before {
  width: 100%;
  left: 0;
}
.nav__link:last-child {
  margin-right: 0;
}
.nav__link-red {
  color: #FF0007;
}
.nav__link-red::before {
  background: #FF0007;
}

.mainprize {
  padding-top: 50px;
}
.mainprize__title {
  margin-bottom: 110px;
}
.mainprize__wintitle {
  width: 100%;
  text-align: center;
  margin-bottom: 30px;
  font-weight: 800;
  font-size: 48px;
  line-height: 50px;
  text-align: center;
  color: #FFFFFF;
  text-shadow: 0px 4px 30px rgba(9, 42, 66, 0.5);
}
.mainprize__list {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}
.mainprize__num {
  font-size: 20px;
  line-height: 40px;
  color: #FFFFFF;
  text-align: center;
}
.mainprize__img {
  width: 580px;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainprize__img img {
  max-width: 100%;
  max-height: 100%;
}
.mainprize__name {
  font-weight: bold;
  font-size: 32px;
  line-height: 35px;
  text-align: center;
  color: #FFFFFF;
  width: 100%;
  display: block;
}
.mainprize .video {
  width: 790px;
  height: 400px;
  margin: 0 auto 45px auto;
}

.mechanics {
  padding-top: 40px;
}
.mechanics__title {
  margin-bottom: 111px;
}

.section__title {
  text-align: center;
  font-weight: 800;
  font-size: 48px;
  line-height: 56px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  text-shadow: 0px 4px 30px rgba(9, 42, 66, 0.5);
  position: relative;
  z-index: 1;
}
.section__title img {
  width: 100%;
  position: absolute;
  left: 0;
  top: -140px;
  display: block;
  z-index: -1;
  max-height: 264px;
}

.m-title {
  font-weight: 800;
  font-size: 32px;
  line-height: 37px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  text-shadow: 0px 4px 10px rgba(9, 42, 66, 0.5);
  width: 100%;
  display: inline-block;
}

.steps {
  position: relative;
  padding-top: 90px;
}
.steps__container {
  width: 1174px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding-bottom: 120px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center bottom;
}
.steps__info {
  position: absolute;
  left: 0;
  top: -40px;
}

.step {
  padding-left: 85px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.step__num {
  position: absolute;
  top: -90px;
  left: 25px;
  font-weight: 800;
  font-size: 398px;
  line-height: 466px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.1);
  text-shadow: 0px 33.1996px 248.997px rgba(9, 42, 66, 0.2);
}
.step__text {
  width: 285px;
  font-weight: bold;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}
.step__text a {
  text-decoration: underline;
  color: #fff;
}
.step__text span {
  font-weight: bold;
  font-size: 15px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  display: block;
}
.step__bg {
  width: 191px;
  height: 157px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 14px;
}

